.Input {
  display: block;
  width: 100%;
  padding: 10px 12px;
  background: none;
  border: solid 1px #e3e7ee;
  border-radius: 4px;
  outline: none;
  line-height: 1.35;

  &::placeholder {
    color: #758eb1;
  }

  &:not(:disabled) {
    &.value,
    &:active,
    &:focus {
      border-color: #1cb6c1;
    }

    &.error {
      border-color: #e65069;
    }
  }
}

.Wrapper {
  position: relative;
}

.Icon {
  display: block;
  position: absolute;
  top: 50%;
  width: 16px;
  height: 16px;
  cursor: pointer;
  right: 12px;
  transform: translateY(-50%);
}
