.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  max-height: 40px;
  border: none;
  background: none;
  outline: none;
  padding: 12px;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
  font-weight: 600;
  text-overflow: ellipsis;
  word-wrap: normal;
  overflow: hidden;

  svg {
    margin-right: 8px;
  }

  &:disabled {
    cursor: default;
    color: #aaaaaa !important;
  }
}

.button {
  &.primary {
    min-width: 95px;
    background-color: #1cb6c1;
    color: #fff;

    &:hover {
      background-color: darken(#1cb6c1, 2%);
    }

    &:disabled {
      opacity: 0.4;
      border-radius: 4px;
      background-color: #1cb6c1;
      color: #aaaaaa !important;
    }
  }

  &.defaults {
    min-width: 95px;
    padding: 11px;
    border: 1px solid #e3e7ee;
    background-color: #ffffff;
    color: #1cb6c1;

    &:hover {
      background-color: darken(#ffffff, 1%);
    }
  }

  &.inverse {
    min-width: 95px;
    padding: 10px 12px;
    border: 1px solid #e3e7ee;
    background-color: #ffffff;
    color: #344563;

    &:hover {
      background-color: darken(#ffffff, 1%);
    }

    &.active {
      background-color: rgba(220, 240, 244, 0.53);
    }
  }

  &.group {
    display: inline-flex;
    min-width: auto;
    width: auto;
    padding-left: 12px;
    padding-right: 12px;
    border: 1px solid #e3e7ee;
    border-radius: 4px;
    font-size: 14px;
    font-weight: normal;
    color: #344563;

    &:first-child {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      border-right: 0;
    }

    &:last-child:not(:first-child) {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }

    &:not(:first-child):not(:last-child) {
      border-radius: 0;
      border-right: 0;
    }

    &:hover {
      background-color: #f1f3f7;
    }

    &.active {
      background-color: rgba(220, 240, 244, 0.53);
      cursor: default;
    }
  }
}
