@import "~components/Forms/Button/Button.module.scss";

.link {
  text-decoration: underline;
  color: #344563;

  &:not(.button) {
    &:hover,
    &:visited {
      color: #344563;
    }
  }
}

.button {
  text-decoration: none;

  @extend .button;

  @extend .defaults;

  &.primary {
    @extend .primary;
  }
}
