.Button {
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
  font-weight: 600;
  color: #748197;

  &:hover {
    &:not(:disabled) {
      text-decoration: underline;
    }

    color: #344563;
  }

  &:disabled {
    color: #e3e7ee;
  }
}

.ButtonActive {
  color: #1cb6c1;
}


