.wrapper {
  display: block;
  width: 100%;
  max-height: 40px;
  border: solid 1px #e3e7ee;
  border-radius: 4px;
  padding-right: 12px;

  select {
    width: 100%;
    padding: 10px 0 10px 10px;
    line-height: 1.35;
    background: none;
    border: none;
    outline: none;
  }

  &:active,
  &:focus {
    border-color: #1cb6c1;
  }
}
