.link {
  display: flex;
  align-items: center;
  padding: 10px 12px 11px 12px;
  border-radius: 4px;
  font-weight: 600;
  color: #344563;
  text-decoration: none;

  > svg {
    margin-right: 8px;
  }

  &:not(:first-child) {
    margin-top: 8px;
  }

  &:hover {
    background-color: #f1f3f7;
  }

  &.active {
    background-color: #ECF7F9;
  }
}
