.Sortable {
  display: inline-flex;
  position: relative;
  cursor: pointer;
  user-select: none;
  background: none;
  border: none;
  outline: none;
  font-weight: inherit;

  &::before,
  &::after {
    content: "";
    display: inline-block;
    width: 3px;
    height: 3px;
    position: absolute;
    right: -16px;
    border: solid #344563;
    border-width: 0 0.115rem 0.115rem 0;
  }

  &::before {
    top: 3px;
    // top: 20%;
    transform: rotate(-135deg);
  }

  &::after {
    // bottom: 20%;

    bottom: 3px;
    transform: rotate(45deg);
  }

  &.up {
    &::before {
      border-color: #1cb6c1;
    }
  }

  &.down::after {
    border-color: #1cb6c1;
  }
}
