.main {
  background: linear-gradient(160deg, #f6f9fc, #f6f9fc 35%, #ecf7f9 35%, #ecf7f9 69%, #f6f9fc 69%);
  display: grid;
  height: 100%;
}

.inner {
  margin: auto;
}

.header {
  margin-bottom: 40px;
  text-align: center;
}

.footer {
  font-weight: 600;
  margin-top: 14px;
  text-align: center;
}

.link {
  color: #1cb6c1;
  text-decoration: none;
}
