.link {
  position: relative;
  padding: 12px 20px;
  display: inline-block;
  color: #344563;
  font-weight: 600;
  text-decoration: none;
  z-index: 1222;

  &.active::after {
    bottom: 0;
    left: 0;
    right: 0;
    content: "";
    height: 2px;
    position: absolute;
    background-color: #00cccc;
  }
}
