.thumbsContainer {
    display: flex;
    flex-flow: row wrap;
    margin-top: 16px;
  };
  
  .thumb  {
    display: inline-flex;
    border-radius: 2px;
    border: 1px solid #eaeaea;
    margin-bottom: 8px;
    margin-right: 8px;
    padding: 4px;
    box-sizing: border-box;
  };
  
  .thumbInner {
    display: flex;
    min-width: 0px;
    overflow: hidden;
  };
  
  .zoneImg {
    align-self: center;
    width: 100%;
  };

  .container {
    display: flex;
    flex-direction: column;
    font-family: sans-serif;    
  }

  .dropzone {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border-width: 2px;
    border-radius: 2px;
    border-color: #eeeeee;
    border-style: dashed;
    background-color: #fafafa;
    color: #bdbdbd;
    outline: none;
    transition: border .24s ease-in-out;
}