
.invoiceListToolbar {
    display: grid;
    grid-template-columns: auto max-content;
    align-items: baseline;
    padding-top: 20px;
}

.invoiceListToolbarTable {
    display: table;
    margin-bottom: 30px;
    width: 100%;
}

.invoiceListToolbarStatusLine {
    height: 30px;
    text-align: right;
    padding-top: 3px;
}